body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.split{
  height: 100%;
  position: fixed;
}

.infoSide{
  width: 75%;
  left: 0;
}

.loginSide{
  width: 25%;
  right: 0;
}

.LoginSide{
    background-color:limegreen;
    height: 100%;
    width:100%;
    top:0;
}

.TitleSpace{
    padding: 50px;
}

.LowerHalf{
    bottom: 0;
    height: 55%;
    width: 100%;
    position: absolute;
}
.TitleText{
    margin: 0;

}
.LoginFormDiv{

}
.InfoSide{
    background-color:rosybrown;
    height: 100%;
    width:100%;
}
